<template>
  <div>
    <h1>常用数据:</h1>
    1、	调用接口
    <p v-html="text1"></p>
    2、 H5表单验证：
    <ul>
      <li>{{text2_1}}</li>
      <li>{{text2_2}}</li>
      <li>{{text2_3}}</li>
    </ul>
    3、公共字段：
    <p v-html="text3"></p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text1: "import { query, api } from @/api/personal_api;",
      text2_1: `
      1、引入显示错误消息盒子
        <validBox refs="validChild" :validBox="valid"></validBox>
        import validBox from "@/views/bank/components/validBoxComp";
        `,
      text2_2: `  
      2、引入消息初始化数据
          valid: {
          showValid: false,
          msg: "",
          phoneClass: "none",
          emailClass: "none"
        },
       `,
      text2_3: `
      3、添加@on-blur="ckEmail"事件
        ckEmail() {
          return this.$refs.validChild.formValid(
          this.formData.email,
          ckEmail,
          "emailClass",
          "邮箱格式错误",
          true
          );
        },`,
      text3: `
      { corpno: "bank000" } 缺少的时候要加`
    };
  },
  created() {
    // this.$alert("欢迎来到我的网站", "欢迎", {
    //   confirmButtonText: "确定",
    //   callback: action => {
    //     console.log(action)
    //   }
    // });
  },
  methods: {}
};
</script>




